<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createLimit('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createLimit('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createLimit('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('TYPE')" icon="far fa-tag">
          <div class="flex justify-start mb-4">
            <ul class="leftx">
              <li v-for="(type, index) in limitTypes" :key="index">
                <vs-radio v-model="limitTypeEnumId" :vs-value="type.enumId">{{ type.description }}</vs-radio>
              </li>
            </ul>
          </div>
        </tab-content>

        <tab-content :title="$t('DETAILS')" icon="far fa-file-alt">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 required">
              <label class="vs-input--label">{{ $t('APP') }}</label>
              <vs-select autocomplete class="w-full" v-model="appId">
                <vs-select-item v-for="(app, index) in apps" :key="index" :value="app.appId" :text="app.appName"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TRANSFER_TYPE') }}</label>
              <funds-transfer-type-lookup :title="$t('TRANSFER_TYPE')" valueField="typeId" displayField="typeName" @select-record="selectTransferTypeRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('FREQUENCY') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="frequencyUomId">
                <vs-select-item v-for="(uom, index) in frequencies" :key="index" :value="uom.uomId" :text="uom.description"/>
              </vs-select>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('CURRENCY_TYPE') }}</label>
              <div class="flex list-style-none p-2">
                <li class="left mr-4" v-for="(enumeration, index) in currencyTypes" :key="index">
                  <vs-radio v-model="currencyTypeEnumId" :vs-value="enumeration.enumId">{{ enumeration.description }}</vs-radio>
                </li>
              </div>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('AMOUNT') }}</label>
              <div class="flex">
                <vs-input-number v-model="minAmount" min="0"/>
                <span class="mt-2 mx-4"> - </span>
                <vs-input-number v-model="maxAmount" min="0"/>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('DEBIT_CURRENCY') }}</label>
              <currency-lookup :title="$t('DEBIT_CURRENCY')" valueField="uomId" displayField="description" @select-record="selectDebitCurrencyRecord"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('TRANSFER_CURRENCY') }}</label>
              <currency-lookup :title="$t('TRANSFER_CURRENCY')" valueField="uomId" displayField="description" @select-record="selectTransferCurrencyRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="limitTypeEnumId === 'LMT_TYPE_USER'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('USER') }}</label>
              <user-lookup :title="$t('USER')" valueField="userId" displayField="userFullName" @select-record="selectUserRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="limitTypeEnumId === 'LMT_TYPE_USER_GRP'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('USER_GROUP') }}</label>
              <user-group-lookup :title="$t('USER_GROUP')" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="limitTypeEnumId === 'LMT_TYPE_ORG'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('PARTY') }}</label>
              <party-lookup :title="$t('PARTY')" valueField="partyId" displayField="partyName" @select-record="selectPartyRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="limitTypeEnumId === 'LMT_TYPE_ACCT'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('ACCOUNT') }}</label>
              <vs-input class="w-full" v-model="accountId"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="limitTypeEnumId === 'LMT_TYPE_BEN'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('BENEFICIARY') }}</label>
              <vs-input class="w-full" v-model="beneficiaryId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
              <nv-input-group>
                <template slot="append">
                  <div class="append-text bg-dark">
                    <i class="far fa-calendar-alt"></i>
                  </div>
                </template>
                <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
              </nv-input-group>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
              <nv-input-group>
                <template slot="append">
                  <div class="append-text bg-dark">
                    <i class="far fa-calendar-alt"></i>
                  </div>
                </template>
                <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
              </nv-input-group>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
              <vs-input class="w-full" v-model="description"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LimitService from '@/services/limit/LimitService'
import AppService from '@/services/commons/AppService'
import EnumerationService from '@/services/commons/EnumerationService'
import UomService from '@/services/commons/UomService'
import { FormWizard, TabContent } from 'vue-form-wizard'
import Datepicker from 'vuejs-datepicker'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import UserLookup from '@/views/nucleus/user/UserLookup'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import FundsTransferTypeLookup from '@/views/ft/funds-transfer-type/FundsTransferTypeLookup'

export default {
  name: 'limit-create',
  mixins: [AlertMixin, DatePickerMixin],
  components: {
    FormWizard,
    TabContent,
    Datepicker,
    CurrencyLookup,
    UserLookup,
    UserGroupLookup,
    PartyLookup,
    FundsTransferTypeLookup
  },
  data: function () {
    return {
      limitTypes: [],
      limitTypeEnumId: 'LMT_TYPE_GENERAL',
      apps: [],
      appId: '',
      frequencies: [],
      frequencyUomId: 'TF_day',
      transferTypeId: '',
      currencyTypes: [],
      currencyTypeEnumId: 'FT_CCY_SAME',
      transferCurrencyId: '',
      debitCurrencyId: '',
      minAmount: 0,
      maxAmount: 0,
      fromDate: null,
      toDate: null,
      description: null,
      disabled: false,
      userId: '',
      userGroupId: '',
      partyId: '',
      accountId: '',
      beneficiaryId: ''
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'LimitType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.limitTypes = response.data.enumerationList || []
      })

      AppService.apps({
      }).then(function (response) {
        self.apps = response.data.appList || []
      })

      UomService.uoms({
        uomTypeEnumId: 'UT_TIME_FREQ_MEASURE',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.frequencies = response.data.uomList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'FundsTransferCurrencyType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.currencyTypes = response.data.enumerationList || []
      })
    },
    selectTransferTypeRecord (record) {
      this.transferTypeId = record.typeId || ''
    },
    selectTransferCurrencyRecord (record) {
      this.transferCurrencyId = record.uomId || ''
    },
    selectDebitCurrencyRecord (record) {
      this.debitCurrencyId = record.uomId || ''
    },
    selectUserRecord (record) {
      this.userId = record.userId || ''
    },
    selectUserGroupRecord (record) {
      this.userGroupId = record.userGroupId || ''
    },
    selectPartyRecord (record) {
      this.partyId = record.partyId || ''
    },
    createLimit (nextAction) {
      let self = this
      LimitService.createFundsTransferLimit({
        limitTypeEnumId: this.limitTypeEnumId,
        appId: this.appId,
        frequencyUomId: this.frequencyUomId,
        transferTypeId: this.transferTypeId,
        currencyTypeEnumId: this.currencyTypeEnumId,
        transferCurrencyId: this.transferCurrencyId,
        debitCurrencyId: this.debitCurrencyId,
        minAmount: this.minAmount,
        maxAmount: this.maxAmount,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate),
        description: this.description,
        disabled: this.disabled ? 'Y' : 'N',
        userId: this.userId,
        userGroupId: this.userGroupId,
        partyId: this.partyId,
        accountId: this.accountId,
        beneficiaryId: this.beneficiaryId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewLimit', params: { id: response.data.limitId } })
          } else {
            self.$router.push({ name: 'BrowseLimits' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
